@import '~assets/styles/h3Imports';

.icon {
  align-items: center;
  border-radius: var(--border-radius) 0 0 var(--border-radius);
  border-right: 1px solid var(--input-default);
  display: flex;
  flex: 0 1 rem(48);
  height: rem(48);
  justify-content: center;
  min-width: var(--inpage-search-icon-min-width, 48px);
  width: rem(48);

  --icon-fill: var(--input-default);
  --icon-stroke: var(--input-default);
  @media #{$tablet-down} {
    [data-fontsize='AA'] & {
      flex: 0 1 2.5rem;
    }

    [data-fontsize='AAA'] & {
      flex: 0 1 1.5rem;
    }
  }

  svg {
    width: rem(24);
  }
}

.wrapper {
  background: var(--white);
  border: 1px solid var(--input-default);
  border-radius: var(--border-radius);
  box-sizing: border-box;
  display: flex;
  overflow: hidden;
  position: relative;

  &:focus-within {
    border: 2px solid var(--input-focus);
    margin: -1px;

    .icon {
      border-right-color: inherit;
    }
  }

  input {
    border: none;
    border-radius: 0 var(--border-radius) var(--border-radius) 0;
    flex: 0 1 auto;
    max-width: calc(100% - 3rem);
    padding: 12px;
    width: 100%;
    @include interactive-text;

    &:focus {
      outline: none;
    }

    &:focus-visible {
      outline: none;
    }

    @media #{$tablet-down} {
      [data-fontsize='AA'] & {
        max-width: calc(100% - 2.5rem);

        &::placeholder {
          font-size: 0.9em;
        }
      }

      [data-fontsize='AAA'] & {
        font-size: 0.75rem;
        max-width: calc(100% - 1.15rem);

        &::placeholder {
          font-size: 0.9em;
        }
      }
    }
  }

  figure + input {
    max-width: calc(100% - 6rem);
  }

  [data-element='clear-search'] {
    align-items: center;
    border-radius: 0 var(--border-radius) var(--border-radius) 0;
    display: flex;
    flex: 0 1 rem(48);
    height: rem(48);
    justify-content: center;
    width: rem(48);
  }
}
